@import url(https://fonts.googleapis.com/css2?family=Grandstander:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Grandstander', cursive;
  margin: 0;
}
.App{
  min-height: 100vh;
 background: url(/cooking-recipe/static/media/2184693.5d0373a3.jpg) center no-repeat;
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
  border-radius: 10px;
}
.search-button{
  background:rgb(90, 89, 89);
  border: none;
  padding: 10px 20px;
  color: white;
  border-radius: 10px;
  margin-left: 5px;
}
.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.navbar{
  width: 100%;
  height: 10vh;
  background: rgb(90, 89, 89);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-button:hover{
  background:white; 
  color:rgb(90, 89, 89);
}
.navbar h1{
  font-size: 64px;
  font-weight: 600;
}
@media (max-width: 576px) {
  .navbar h1{
    font-size: 32px;
    font-weight: 600;
  }
  .App{
    min-height: 100vh;
    background: url(/cooking-recipe/static/media/2184693.5d0373a3.jpg)  no-repeat;
  }

}

/* Medium devices (tablets, 768px and up) */
@media (max-width: 768px) { 
  .navbar h1{
    font-size: 32px;
    font-weight: 600;
  }
  .App{
    min-height: 100vh;
    background: url(/cooking-recipe/static/media/2184693.5d0373a3.jpg)  no-repeat;
  }
}

.recipe_recipe__2Mggp{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(0, 0, 0);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background:rgb(90, 89, 89);
    align-items: center;
    min-width: 40%;
    padding: 10px;
    color: white;
}
.recipe_image__230xO{
    border-radius: 25%;
    width: 100px;
    height: 100px;
    margin-bottom: 5px;
}
h1{
    font-family: 'Dancing Script', cursive;
    margin-bottom: 10px;
}
p{
    margin-bottom: 10px;
}
ol{
    margin-bottom: 10px;
}
.recipe_recipe__2Mggp:hover{
    background:white;    
    color: rgb(90, 89, 89); 
    transition: all 0.5s;
}
